<template>
  <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
    <el-form-item label="水表期初读数" prop="cost_w_s_number">
      <div class="label">{{ ruleForm.cost_w_s_number }}</div>
    </el-form-item>
    <el-form-item label="水表期末读数" prop="cost_w_e_number">
      <el-input type="text" v-model="ruleForm.cost_w_e_number" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="电表期初读数" prop="cost_e_s_number">
      <div class="label">{{ ruleForm.cost_e_s_number }}</div>
    </el-form-item>
    <el-form-item label="电表期末读数" prop="cost_e_e_number">
      <el-input type="text" v-model="ruleForm.cost_e_e_number" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="气表期初读数" prop="cost_g_s_number">
      <div class="label">{{ ruleForm.cost_g_s_number }}</div>
    </el-form-item>
    <el-form-item label="气表期末读数" prop="cost_g_e_number">
      <el-input type="text" v-model="ruleForm.cost_g_e_number" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')">计算</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import api from '@/api/api.js'
import { mapState } from 'vuex'

export default {
  name: 'Calculate',
  data () {
    return {
      ruleForm: {
      },
      rules: {
      }
    }
  },
  computed: {
    ...mapState(['userInfo']),
  },
  methods: {
    getInit(){
      api.initCost({user_id: this.userInfo.user_id}).then(res => {
        if (res.code === 1) {
          this.ruleForm = {...res.data}
        } else {
          this.$message(res.message)
        }
      })
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          api.calculateCost({...this.ruleForm, cost_contract_no: this.userInfo.user_contract_no}).then(res => {
            if (res.code === 1) {
              this.$store.commit('setCalculate',res.data)
              this.$router.push('/Cost')
            } else {
              this.$message(res.message)
            }
          })
        } else {
          return false
        }
      })
    }
  },
  mounted(){
    this.getInit()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.demo-ruleForm{
  width: 300px;
  margin: 100px auto 0;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.label{
  text-align: left;
}
</style>



