<template>
  <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="80px" class="demo-ruleForm">
    <el-form-item label="水表读数" prop="cost_w_number">
      <el-input type="text" v-model="ruleForm.cost_w_number" autocomplete="off" disabled></el-input>
    </el-form-item>
    <el-form-item label="水费" prop="cost_w_money">
      <el-input type="text" v-model="ruleForm.cost_w_money" autocomplete="off" disabled></el-input>
    </el-form-item>
    <el-form-item label="电表读数" prop="cost_e_number">
      <el-input type="text" v-model="ruleForm.cost_e_number" autocomplete="off" disabled></el-input>
    </el-form-item>
    <el-form-item label="电费" prop="cost_e_money">
      <el-input type="text" v-model="ruleForm.cost_e_money" autocomplete="off" disabled></el-input>
    </el-form-item>
    <el-form-item label="气表读数" prop="cost_g_number">
      <el-input type="text" v-model="ruleForm.cost_g_number" autocomplete="off" disabled></el-input>
    </el-form-item>
    <el-form-item label="气费" prop="cost_g_money">
      <el-input type="text" v-model="ruleForm.cost_g_money" autocomplete="off" disabled></el-input>
    </el-form-item>
    <el-form-item label="合计" prop="cost_total_money">
      <el-input type="text" v-model="ruleForm.cost_total_money" autocomplete="off" disabled></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import api from '@/api/api.js'
import { mapState } from 'vuex'
export default {
  name: 'Calculate',
  data() {
    return {
      rules: {
      }
    }
  },
  methods: {
    moneyFormat(num, decimal = 2, split = ',') {
      /*
        parameter：
        num：格式化目标数字
        decimal：保留几位小数，默认2位
        split：千分位分隔符，默认为,
        moneyFormat(123456789.87654321, 2, ',') // 123,456,789.88
      */
      if (isFinite(num)) { // num是数字
        if (num === 0) { // 为0
          return num.toFixed(decimal)
        } else { // 非0
          var res = ''
          var dotIndex = String(num).indexOf('.')
          if (dotIndex === -1) { // 整数
            res = String(num).replace(/(\d)(?=(?:\d{3})+$)/g, `$1${split}`) + '.' + '0'.repeat(decimal)
          } else { // 非整数
            // js四舍五入 Math.round()：正数时4舍5入，负数时5舍6入
            // Math.round(1.5) = 2
            // Math.round(-1.5) = -1
            // Math.round(-1.6) = -2
            // 保留decimals位小数
            const numStr = String((Math.round(num * Math.pow(10, decimal)) / Math.pow(10, decimal)).toFixed(decimal)) // 四舍五入，然后固定保留2位小数
            const decimals = numStr.slice(dotIndex, dotIndex + decimal + 1) // 截取小数位
            res = String(numStr.slice(0, dotIndex)).replace(/(\d)(?=(?:\d{3})+$)/g, `$1${split}`) + decimals
          }
          return res
        }
      } else {
        return '--'
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          api.saveCost({ ...this.ruleForm}).then(res => {
            this.$message(res.message)
          })
        } else {
          return false
        }
      })
    }
  },
  computed: {
    ...mapState(['calculateData']),
    ruleForm() {
      let data = {...this.calculateData}
      for(let key in data){
        if(key.indexOf('_money')!=-1){
          data[key] = this.moneyFormat(data[key])
        }
      }
      return data
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.demo-ruleForm {
  width: 300px;
  margin: 100px auto 0;
}

h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}</style>



